import React, { useState } from 'react';
import { useHistory, Link } from 'react-router-dom';

const AppBar = (props) => {
	const [ input, setInput ] = useState('');

	let history = useHistory();

	// const logout = () => {
	// 	let OneSignal = window.OneSignal || [];
	// 	OneSignal.push(function() {
	// 		OneSignal.removeExternalUserId();
	// 	});
	// 	localStorage.removeItem('userId');
	// 	props.setUser({ id: '', name: '', authenticated: false });
	// };

	const handleSearch = (e) => {
		e.preventDefault();
		history.push(`/search/${input}`);
	};

	return (
		<div id="app-bar">
			<div className="ui container small secondary menu">
				<span className="item active cursor-pointer" onClick={() => history.replace('/')}>
					H
				</span>
				<div className="item" id="search-bar">
					<form action="/search/" method="GET" className="ui icon input" onSubmit={(e) => handleSearch(e)}>
						<input
							type="text"
							name="q"
							placeholder="Search anime titles..."
							value={input}
							onChange={(e) => setInput(e.target.value)}
						/>
						<i className="search link icon" />
					</form>
				</div>
				{!props.user.authenticated && (
					<Link to="/auth/login" className="item active cursor-pointer">
						AU
					</Link>
				)}
			</div>
		</div>
	);
};

export default AppBar;
